// Dropdown alterations for DTNA

// Places a down arrow to the right of any <a> with a class of dropdown-toggle
.dropdown-toggle::after {
&:extend(.caret);
content: "";
}

// Fixes bug that doesn't allow selection on dropdown menu items
.dropdown-backdrop{
	position: static;
}

// Hover drop for desktop viewports
@media (min-width: @grid-float-breakpoint) {
  .dropdown:hover .dropdown-menu {
    display: block;
  }
}


// Style for dropdown box on desktop viewports
@media (min-width: @grid-float-breakpoint) {
  .horiz-menu .dropdown-menu {
    border-radius: 0;
    border-top: 2px solid @gray;
    border-bottom: 2px solid @gray;
    .drop-shadow (0, 0, 1px, 0, 0, 0, 0, .5);

    li {
      border: none;
    }

    a {
      color: @gray-darker;
      text-shadow: -1px, -1px, 2px, rgba(0, 0, 0, .5);
      border: none;

      &:hover,
      &:focus {
        border: none;
      }
    }
  }
}
    

// Styles for mega dropdown menu
.megadrop {
  &:extend(.horiz-menu all);
  position: relative;

  // Aligns dropdown relative to the overall nav menu container (megadrop-menu) rather than individual menu items
  .dropdown {
	position: static;


  }

  .dropdown-toggle::after {
	content: none;
  }

  li > a {
  	color: @brand-primary;
  }

  @media (min-width: @grid-float-breakpoint) {
	.dropdown > a{
	  &:hover,
    &:focus {
        border-bottom: 4px solid @brand-primary;
    }
  }

  	.dropdown-menu {
  	  border-top: 3px solid @brand-primary;
  	  border-bottom: 1px solid @brand-primary;
  	  border-radius: 0;
  	  width: 100%;
  	  .drop-shadow (0, 0, 1px, 0, 0, 0, 0, .5);
  	}
  }
}