.head-content-foot, 
.head-content-right-foot, 
.head-left-content-right-foot, 
.head-content, 
.head-left-content-foot, 
.head-3-col {
	.container;
}

/*Head content foot page layout*/
.head-content-foot > * {
	.make-xs-column(12);
	.make-sm-column(12);
	.make-md-column(12);
	.make-lg-column(12);
	.remove-padding();
}
.head-content-foot > :nth-child(2) {
	min-height: 75vh;
}


/*Head content right foot page layout*/
.head-content-right-foot > :nth-child(1), .head-content-right-foot > :nth-child(3) {
	.make-xs-column(12);
	.make-sm-column(12);
	.make-md-column(12);
	.make-lg-column(12);
	.remove-padding();
}
.head-content-right-foot > :nth-child(2) {
	.make-row();
	> :nth-child(1) {
		.make-xs-column(12);
		.make-sm-column(8);
		.make-md-column(9);
		.make-lg-column(9);
		/*min-height: 75vh;*/
	}
	> :nth-child(2) {
		.make-xs-column(12);
		.make-sm-column(4);
		.make-md-column(3);
		.make-lg-column(3);
		/*min-height: 75vh;*/
	}
}


/*Head left content right foot page layout*/
.head-left-content-right-foot > :nth-child(1), .head-left-content-right-foot > :nth-child(3) {
	.make-xs-column(12);
	.make-sm-column(12);
	.make-md-column(12);
	.make-lg-column(12);
	.remove-padding();
}
.head-left-content-right-foot > :nth-child(2) { 
	.make-row();
	> :nth-child(1) {
		.make-xs-column(12);
		.make-sm-column(4);
		.make-md-column(3);
		.make-lg-column(3);
		/*min-height: 75vh;*/
	}
	> :nth-child(3) {
		.make-xs-column(12);
		.make-sm-column(12);
		.make-md-column(3);
		.make-lg-column(3);
		/*min-height: 75vh;*/
	}
	> :nth-child(2) {
		.make-xs-column(12);
		.make-sm-column(8);
		.make-md-column(6);
		.make-lg-column(6);
		/*min-height: 75vh;*/
	}
}

/*Head content page layout*/
.head-content {
	.head-content-foot;
}


/*Head left content foot page layout*/
.head-left-content-foot > :nth-child(1), .head-left-content-foot > :nth-child(3) {
	.make-xs-column(12);
	.make-sm-column(12);
	.make-md-column(12);
	.make-lg-column(12);
	.remove-padding();
}
.head-left-content-foot > :nth-child(2) {
	.make-row();
	> :nth-child(1) {
		.make-xs-column(12);
		.make-sm-column(12);
		.make-md-column(3);
		.make-lg-column(3);
		/*min-height: 75vh;*/

	}
	> :nth-child(2) {
		.make-xs-column(12);
		.make-sm-column(12);
		.make-md-column(9);
		.make-lg-column(9);
		/*min-height: 75vh;*/
	}
}
.hidden-mobile {
	.hidden-xs;
	.hidden-sm;
}
.hidden-desktop {
	.hidden-md;
	.hidden-lg;
}


