//
// Panels dtna specific
// --------------------------------------------------

.tile-up {
	&:extend(.panel all);
	&:extend(.clearfix all);
	max-height: 1299px;
	padding: 0;

	> *:nth-child(1) {
		&:extend(.panel-heading);
	}

	> *:not(:first-child) {
  	padding: @panel-body-padding;
	}

	&:extend(.tile-up.default all);
}

.tile-up-image {
	&:extend(.tile-up all);
		> *:not(:first-child) {
		padding: 0 !important;

		> img {
			width:100%;
		}
	}
}

.tile-down {
	&:extend(.panel all);
	&:extend(.clearfix all);
	max-height: 1299px;
	padding: 0;

	> *:last-child {
		padding: @panel-heading-padding;
		background-color: @panel-default-heading-bg;
		color: @panel-default-text;

		>* {
			margin-top: 0;
		  margin-bottom: 0;
		}
	}

	> *:not(:last-child) {
  	padding: @panel-body-padding;
  	background-color: @panel-bg;
  	color: @text-color;
	}

	&:extend(.tile-down.default all);
}

.tile-down-image {
	&:extend(.tile-down all);
	> *:nth-child(1) {
		padding: 0 !important;

		> img {
			width:100%;
		}
	}
}


.tile-up-variant(@border; @heading-text-color; @heading-bg-color; @heading-border) {
  border-color: @border;

  & > *:nth-child(1) {
    color: @heading-text-color;
    background-color: @heading-bg-color;
    border-color: @heading-border;

    & > *:nth-child(1) {
	     margin-top: 0;
		  margin-bottom: 0;
		  color: inherit;
	    }

	& > *:nth-child(2) {
	    padding: @panel-body-padding;
	  &:extend(.clearfix all);  
    }
  }
}

.tile-up-image-variant(@border; @heading-text-color; @heading-bg-color; @heading-border) {
  border-color: @border;

  & > *:nth-child(1) {
    color: @heading-text-color;
    background-color: @heading-bg-color;
    border-color: @heading-border;

    & > *:nth-child(1) {
	     margin-top: 0;
		  margin-bottom: 0;
		  color: inherit;
	    }

	& > *:nth-child(2) {
	    padding: @panel-body-padding;
	  &:extend(.clearfix all);  
    }
  }
}

.tile-down-variant(@border; @heading-text-color; @heading-bg-color; @heading-border) {
  border-color: @border;

  & > *:last-child {
    color: @heading-text-color;
    background-color: @heading-bg-color;
    border-color: red;

    & > *:nth-child(1) {
	     margin-top: 0;
		  margin-bottom: 0;
		  color: inherit;
	    }

	& > *:not(:last-child) {
	    padding: @panel-body-padding;
	    background-color: @panel-bg;
	  &:extend(.clearfix all);  
    }
  }
}

.tile-down-image-variant(@border; @heading-text-color; @heading-bg-color; @heading-border) {
  border-color: @border;

  & > *:last-child {
    color: @heading-text-color;
    background-color: @heading-bg-color;
    border-color: @heading-border;

    & > *:nth-child(1) {
	     margin-top: 0;
		  margin-bottom: 0;
		  color: inherit;
	    }

	& > *:not(:last-child) {
	    padding: @panel-body-padding;
	    background-color: @panel-bg;
	  &:extend(.clearfix all);  
    }
  }
}

// Contextual variations
.tile-up.default {
  .tile-up-variant(@panel-default-border; @panel-default-text; @panel-default-heading-bg; @panel-default-border);
}
.tile-up.primary {
  .tile-up-variant(@panel-primary-border; @panel-primary-text; @panel-primary-heading-bg; @panel-primary-border);
}
.tile.success {
  .tile-up-variant(@panel-success-border; @panel-success-text; @panel-success-heading-bg; @panel-success-border);
}
.tile-up.info {
  .tile-up-variant(@panel-info-border; @panel-info-text; @panel-info-heading-bg; @panel-info-border);
}
.tile-up.warning {
  .tile-up-variant(@panel-warning-border; @panel-warning-text; @panel-warning-heading-bg; @panel-warning-border);
}
.tile-up.danger {
  .tile-up-variant(@panel-danger-border; @panel-danger-text; @panel-danger-heading-bg; @panel-danger-border);
}

.tile-up-image.danger {
  .tile-up-image-variant(@panel-danger-border; @panel-danger-text; @panel-danger-heading-bg; @panel-danger-border);
}


// Contextual variations
.tile-down.default {
  .tile-down-variant(@panel-default-border; @panel-default-text; @panel-default-heading-bg; @panel-default-border);
}
.tile-down.primary {
  .tile-down-variant(@panel-primary-border; @panel-primary-text; @panel-primary-heading-bg; @panel-primary-border);
}
.tile-down.success {
  .tile-down-variant(@panel-success-border; @panel-success-text; @panel-success-heading-bg; @panel-success-border);
}
.tile-down.info {
  .tile-down-variant(@panel-info-border; @panel-info-text; @panel-info-heading-bg; @panel-info-border);
}
.tile-down.warning {
  .tile-down-variant(@panel-warning-border; @panel-warning-text; @panel-warning-heading-bg; @panel-warning-border);
}
.tile-down.danger {
  .tile-down-variant(@panel-danger-border; @panel-danger-text; @panel-danger-heading-bg; @panel-danger-border);
}


.tile-down.info::before, .tile-up.info::before { 
	display:none;
}

