//
// DTNA Tables
// --------------------------------------------------

.dtna_table_stacked {
  &:extend(.table all);
  width: 100%;

  /* following code from: http://css-tricks.com/responsive-data-tables/ */
  @media 
  only screen and (max-width: 760px),
  (min-device-width: 768px) and (max-device-width: 1024px)  {
  
    /* Force table to not be like tables anymore */
    table, thead, tbody, th, td, tr { 
      display: block; 
    }
    
    /* Hide table headers (but not display: none;, for accessibility) */
    thead tr { 
      position: absolute;
      top: -9999px;
      left: -9999px;
    }

    tr { border: 1px solid #ccc; }
    
    td { 
      /* Behave  like a "row" */
      border: none;
      border-bottom: 1px solid #eee; 
      position: relative;
      padding-left: 50%; 
      text-align: right;
    }
    
    td:before { 
      /* Now like a table header */
      position: absolute;
      /* Top/left values mimic padding */
      left: 6px;
      width: 45%; 
      font-weight: bold;
      padding-right: 10px; 
      white-space: nowrap;
      text-align: left;
    }

    
    /* Add a pseudoclass to each td that you are goign to fill with values from the "th" via javascript*/
    td:nth-of-type(n):before { content: attr(data-content) ''; 
    width: 50%;
    overflow: hidden;
    text-overflow:ellipsis;
    }
  }
  
  /* Smartphones (portrait and landscape) ----------- */
  @media only screen
  and (min-device-width : 320px)
  and (max-device-width : 480px) {
    body { 
      padding: 0; 
      margin: 0; 
      width: 320px; }
    }
  
  /* iPads (portrait and landscape) ----------- */
  @media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
    body { 
      width: 495px; 
      
    }
  }
}

.striped {
  &:extend(.table-striped all);
  &:extend(.table-hover all);
}

.toggletable {
  display: none;
}  

.dtna_table_wrapper {
  width:100%;
  overflow-x:hidden;
}

.dtna_table_wrapper_scrolling {
  overflow-x:scroll;
  padding-bottom: 1rem;
  margin-bottom: 1rem;

  > table > thead > tr > th, > table > tbody > tr > td {
    padding-left: 1rem;
    vertical-align: top;
  }
}



.hidden_table {
  display: none;
}

table td, table th {
  vertical-align: top !important;
}

.stacked_mobile_table {
  table-layout: fixed;
  width:100%;
  margin-bottom: .5rem;

  td {
    width: 100px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: left;
    font-weight: bold;
    vertical-align: top;
  }
  
  td:nth-child(even) {
    text-align: left;
    font-weight: normal;
  }
}

.mobile_table_container table:nth-child(odd){
  background-color: #f0f0f0;
}

.desktop_table_container {
  &:extend(.table all);

  width:100%;
  overflow-x:auto;
  display: none;

  td, tr {
    padding: 0 .5rem 0 .5rem;
  }
}

.mobile_table_container {
  display: block;
}

@media only screen and (min-device-width: 580px) {
  .dtna_table_stacked_controller, .mobile_table_container { 
    display: none; 
  }

  .desktop_table_container {
    display: block;
  }

  .dtna_table_stacked {
    display: block;
  }
}






