// Shadow effects

//Usage
// .drop-shadow(x-offset, y-offset, blur, spread, r, g, b, alpha);
// Example: .drop-shadow (1, 2, 1, 0, 50, 50, 50, .025);
// --prints:  -webkit-boxshadow: 1, 2, 1, 0, rgba(50, 50, 50, .25);
//        -moz-box-shadow: 1, 2, 1, 0, rgba(50, 50, 50, .25);
//        box-shadow: 1, 2, 1, 0, rgba(50, 50, 50, .25);

// Drop Shadow 
.drop-shadow (@x: 0, @y: 1px, @blur: 1px, @spread: 0, @r: 50, @g: 50, @b: 50, @alpha: 0.5) {
	-webkit-box-shadow:	@x @y @blur @spread rgba(@r, @g, @b, @alpha);
	-moz-box-shadow:	@x @y @blur @spread rgba(@r, @g, @b, @alpha);
	box-shadow:		@x @y @blur @spread rgba(@r, @g, @b, @alpha);
}

// Ineer Shadow
.inner-shadow (@x: 0, @y: 1px, @blur: 2px, @spread: 0, @r: 50, @g: 50, @b: 50, @alpha: 0.25) {
  -webkit-box-shadow: inset @x @y @blur @spread rgba(@r, @g, @b, @alpha);
  -moz-box-shadow:    inset @x @y @blur @spread rgba(@r, @g, @b, @alpha);
  box-shadow:         inset @x @y @blur @spread rgba(@r, @g, @b, @alpha);
}


.nav-divider(@color: #e5e5e5) {
  height: 1px;
  margin: (@line-height-computed / 4) 0;
  overflow: hidden;
  background-color: @color;
}



