

// Block-link ----------------------------
// sets up basic requirements for any block link
.block-link {
	position: absolute; 
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: 1;
}

// Small vertically stacked series of links ----------
.link-list {
	> * {
	display: block;
	margin: 0 1rem;
	padding: .5rem 0;
	}

	a {
		&:focus,
		&:hover {
			padding-left: .25rem;
			text-decoration: none;
			background-color: rgba(50,50,50,.05);
			border-left: 2px solid @brand-primary;
		}

		&:active {
			padding-left: .25rem;
			background-color: rgba(50,50,50,.1);
			border-left: 2px solid @brand-dark;
		}
	}
}


// Large blocks of content stacked vertically --------
.group-list {
	> * {
	position: relative;
	display: block;
	margin: 1rem .5rem 0;
	padding: .5rem 1rem;
	}

	&:not(:first-child) {
		padding-top: .5rem;
	}

	a {
		text-decoration: none;
	}

	a li {
		text-overflow: ellipsis;
		white-space: nowrap;
		overflow: hidden;
	}

	.block-link {
		&:focus,
		&:hover {
			background-color: rgba(50,50,50,.05);
			border-left: 4px solid @brand-primary;
		}

		&:active {
			background-color: rgba(50,50,50,.1);
			border-left: 4px solid @brand-dark;
		}
	}
}

