// Navs that are styled for DTNA


// All horiztonal nav common styles
.horizontal-nav {
  &:extend(.navbar all); // Adds .horizontal to all .navbar style declaration in CSS Output
  margin-bottom: .125rem;
  border-radius: 0;
  .drop-shadow; // Default drop shadow (prints vendor prefixes automatically) 
  // box-shadow: (0, 1px, 1px, 0, rgba(50, 50, 50, 0.5);

  [data-toggle="collapse"] {
    &:extend(.navbar-toggle all); //Adds .navbar-toggle declarations to data-toggle="collapse" (added to <button> in navs)
    margin-top: .5rem;
    margin-right: @navbar-padding-horizontal;
    margin-bottom: .5rem;
  }
}

//Allows you to add permanent elements to all states of a nav
.nav-header {
  &:extend(.navbar-header all); //Adds .nav-header too all .navbar-header declarations
}

//Simple button branding for navigation. Will allways appear on the far left of the nav. Shoudl replace home button.
.nav-brand {
  &:extend(.navbar-brand all);
  margin: .125rem;
  padding-right: 1.875rem;
  padding-left: 1.875rem;
  font-weight: bold;
  color: @daimler-blue;
  background-color: #ffffff;
  .drop-shadow(@x: 1px, @alpha: .25);
}


// Default menu styles for horiztonal list items
.horiz-menu {
  &:extend(.navbar-collapse all); // Adds .horiz-menu to all .navbar-collapse style declaration in CSS Output
  > ul {
    &:extend(.nav all); // Adds .horiz-menu to all .nav style declaration in CSS Output
    &:extend(.navbar-nav all); // Adds .horiz-menu to all .navbar-nav style declaration in CSS Output
    border-radius: 0px;

    a {
      &:focus,
      &:hover {
        text-decoration: none;
        background-color: @gray-lightest;
        border-left: 2px solid @brand-primary;
      }

      &:active {
        background-color: @gray-lightest;
        border-left: 2px solid @brand-dark;
      }
    }

    .current-menu-item a {
      color: @daimler-blue;
      cursor: default;
      background-color: @gray-lightest;
    }

    @media (min-width: @grid-float-breakpoint) {
      > :not(:last-child) {
        border-right: 1px solid @navbar-default-border;
      }

      > :not(:first-child) {
        border-left: 1px solid white;
      }

      li a {
        border-left: white;
        border-bottom: 4px solid @gray-lighter;
        &:hover,
        &:focus {
          background-color: @gray-lightest!important;
          border-bottom: 4px solid @gray;
          border-left: none;
        }

        &:active {
          border-bottom: 4px solid @brand-lighter;
        }
      }

      .current-menu-item a {
        border-bottom: 4px solid @brand-dark;
        background-color: transparent;
        &:hover,
        &:focus {
          border-bottom: 4px solid @brand-dark;
          border-left: none;
        }
      }
    }
  } 
}


// Scrollspy
// -----------------------------------

#myScrollspy{
  .hidden-sm;
  .hidden-xs;
}

.scrspy.affix {
  top: 2rem;
}

.scrspy {
  float: none;
  width: 14.5rem;
  padding: 1.25rem 0 0 0;
  margin: 0;
  ul{
    padding: 0;
    margin: 0;
  }
  li {
    padding: 0;
    margin: .125rem 0 .125rem .625rem;
  }
  a{
    padding: .25rem 0 .25rem .5rem !important;
    margin: 0 !important;
  }
}

.scrspy.affix-top{
  width: 14.5rem;
}

.scrspy li ul li {
  font-size: 80%;
}
.scrspy li ul li ul li{
  font-size: inherit;
}

.scrspy li.active a:hover {
  color: @daimler-blue;
  border-right: 1px solid @daimler-blue;
}
ul > li.active > a {
  color: @daimler-blue;
  border-right: 1px solid @daimler-blue;
}
li.active, li.active a:hover {
  color: none;
  border-right: none;
}

.navbar-fixed-top {
  background-color: f8f8f8;
  .drop-shadow;

  .horizontal-nav {
  margin: auto;
  border: none;
  background-color: none;
  .drop-shadow(0, 0, 0, 0, 0, 0, 0, 0,); // Default drop shadow (prints vendor prefixes automatically) 
  // box-shadow: (0, 1px, 1px, 0, rgba(50, 50, 50, 0.5);
  }

 .nav-brand{
  .drop-shadow(0, 0, 0, 0, 0, 0, 0, 0,);
 }

}